@import url(https://fonts.googleapis.com/css?family=Poppins,Indie+Flower,Josefin+Sans);
#scrollArrow {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  padding: 5px;
  transition: opacity 0.5 ease-in;
  opacity: 0.4;
  position: fixed;
  bottom: 1rem;
  right: 1rem;
}

#scrollArrow:hover {
  opacity: 1;
}

.navbar {
  background: #0b3d6b;
  margin-bottom: 1rem;
  display: inline-block;
}

.navbar ul li{
  list-style: none;
}

.navbar span {
  color: #fff;
  font-size: 20px;

}

body {
  margin: 0;

  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  font-family: "Josefin Sans";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #83a4d4; /* Fallback for older browser */
  background: linear-gradient(to left, #b6fbff, #83a4d4);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1,
h2,
h3,
h4,
h5 {
  font-family: "Poppins";
}

h1 {
  text-align: center;
  font-weight: 700;
  font-size: 48px;
  letter-spacing: 2px;
  margin-bottom: 1.5rem;
}

